'use client'

import React, { useState } from 'react'
import { useRouter } from 'next/navigation'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Skeleton } from '@/components/ui/skeleton'
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Progress } from '@/components/ui/progress'
import { useToast } from '@/hooks/use-toast'
import { 
  ArrowLeft,
  CreditCard,
  Download,
  AlertCircle,
  RefreshCw,
  Crown,
  Calendar,
  Users,
  MessageSquare,
  UserCheck,
  TrendingUp,
  Check,
  X,
  DollarSign,
  FileText,
  ExternalLink,
  Shield,
  Zap,
  Star
} from 'lucide-react'
import { 
  useBillingInfo, 
  useUpdatePaymentMethod, 
  useChangePlan,
  useCancelSubscription,
  useDownloadInvoice,
  type BillingInfo 
} from '@/lib/api/settings'

const paymentMethodSchema = z.object({
  token: z.string().min(1, '  ')
})

const cancelSubscriptionSchema = z.object({
  reason: z.string().min(10, '  10  ').max(500, '  500   ')
})

type PaymentMethodForm = z.infer<typeof paymentMethodSchema>
type CancelSubscriptionForm = z.infer<typeof cancelSubscriptionSchema>

const plans = [
  {
    id: 'starter',
    name: 'Starter',
    price: 29000,
    period: 'month',
    description: '      ',
    features: [
      ' 5',
      ' 20', 
      ' 5,000',
      ' 10,000/',
      ' ',
      ' '
    ],
    popular: false
  },
  {
    id: 'professional',
    name: 'Professional',
    price: 99000,
    period: 'month', 
    description: '    ',
    features: [
      ' 25',
      ' 100',
      ' 50,000', 
      ' 100,000/',
      '   ',
      ' ',
      'API ',
      ' '
    ],
    popular: true
  },
  {
    id: 'enterprise',
    name: 'Enterprise',
    price: 299000,
    period: 'month',
    description: '    ',
    features: [
      ' ',
      ' ',
      ' ',
      ' 1,000,000/',
      '  +  ',
      '  ',
      ' ',
      'SSO ',
      '  +  '
    ],
    popular: false
  }
]

export default function BillingSettingsPage() {
  const router = useRouter()
  const { toast } = useToast()
  const [selectedPlan, setSelectedPlan] = useState<string | null>(null)
  const [isChangePlanDialogOpen, setIsChangePlanDialogOpen] = useState(false)
  const [isCancelDialogOpen, setIsCancelDialogOpen] = useState(false)
  const [isPaymentDialogOpen, setIsPaymentDialogOpen] = useState(false)
  
  const { 
    data: billingInfo, 
    isLoading, 
    error,
    refetch 
  } = useBillingInfo()
  
  const updatePaymentMethod = useUpdatePaymentMethod()
  const changePlan = useChangePlan()
  const cancelSubscription = useCancelSubscription()
  const downloadInvoice = useDownloadInvoice()

  const paymentForm = useForm<PaymentMethodForm>({
    resolver: zodResolver(paymentMethodSchema),
    defaultValues: {
      token: ''
    }
  })

  const cancelForm = useForm<CancelSubscriptionForm>({
    resolver: zodResolver(cancelSubscriptionSchema),
    defaultValues: {
      reason: ''
    }
  })

  const handleChangePlan = async (planId: string) => {
    try {
      await changePlan.mutateAsync({ planId })
      toast({
        title: "  ",
        description: `${plans.find(p => p.id === planId)?.name}   .`,
      })
      setIsChangePlanDialogOpen(false)
      setSelectedPlan(null)
    } catch (error) {
      toast({
        title: "  ",
        description: "    .  .",
        variant: "destructive",
      })
    }
  }

  const handleUpdatePaymentMethod = async (data: PaymentMethodForm) => {
    try {
      await updatePaymentMethod.mutateAsync(data)
      toast({
        title: "   ",
        description: "   .",
      })
      setIsPaymentDialogOpen(false)
      paymentForm.reset()
    } catch (error) {
      toast({
        title: " ",
        description: "     .",
        variant: "destructive",
      })
    }
  }

  const handleCancelSubscription = async (data: CancelSubscriptionForm) => {
    try {
      await cancelSubscription.mutateAsync({
        cancelAtPeriodEnd: true,
        reason: data.reason
      })
      toast({
        title: "   ",
        description: "      .",
      })
      setIsCancelDialogOpen(false)
      cancelForm.reset()
    } catch (error) {
      toast({
        title: "  ", 
        description: "     .",
        variant: "destructive",
      })
    }
  }

  const handleDownloadInvoice = async (invoiceId: string) => {
    try {
      await downloadInvoice.mutateAsync(invoiceId)
    } catch (error) {
      toast({
        title: " ",
        description: "    .",
        variant: "destructive",
      })
    }
  }

  const getUsagePercentage = (current: number, limit: number) => {
    return limit === 0 ? 0 : Math.min((current / limit) * 100, 100)
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active':
        return 'bg-green-100 text-green-700 border-green-300'
      case 'trial':
        return 'bg-blue-100 text-blue-700 border-blue-300'
      case 'suspended':
        return 'bg-orange-100 text-orange-700 border-orange-300'
      case 'cancelled':
        return 'bg-red-100 text-red-700 border-red-300'
      default:
        return 'bg-gray-100 text-gray-700 border-gray-300'
    }
  }

  const getInvoiceStatusColor = (status: string) => {
    switch (status) {
      case 'paid':
        return 'bg-green-100 text-green-700'
      case 'pending':
        return 'bg-yellow-100 text-yellow-700'
      case 'failed':
        return 'bg-red-100 text-red-700'
      default:
        return 'bg-gray-100 text-gray-700'
    }
  }

  if (error) {
    return (
      <div className="container mx-auto p-6">
        <Alert className="border-red-200">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription className="flex items-center justify-between">
            <span>   : {error.message}</span>
            <Button variant="outline" size="sm" onClick={() => refetch()}>
              <RefreshCw className="h-3 w-3 mr-1" />
               
            </Button>
          </AlertDescription>
        </Alert>
      </div>
    )
  }

  return (
    <div className="container mx-auto p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4">
        <Button
          variant="ghost"
          size="sm"
          onClick={() => router.back()}
          className="gap-2"
        >
          <ArrowLeft className="h-4 w-4" />
          
        </Button>
        <div>
          <h1 className="text-3xl font-bold text-gray-900"> </h1>
          <p className="text-gray-600">
            ,  ,  
          </p>
        </div>
      </div>

      {/* Current Subscription */}
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <Crown className="h-5 w-5" />
            <div>
              <CardTitle> </CardTitle>
              <CardDescription>
                   
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {isLoading ? (
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <div className="space-y-2">
                  <Skeleton className="h-6 w-32" />
                  <Skeleton className="h-4 w-48" />
                </div>
                <Skeleton className="h-8 w-24" />
              </div>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                {[...Array(4)].map((_, i) => (
                  <div key={i} className="text-center">
                    <Skeleton className="h-6 w-16 mx-auto mb-1" />
                    <Skeleton className="h-4 w-20 mx-auto" />
                  </div>
                ))}
              </div>
            </div>
          ) : billingInfo ? (
            <>
              {/* Subscription Info */}
              <div className="flex items-center justify-between mb-6">
                <div>
                  <div className="flex items-center gap-3 mb-2">
                    <h3 className="text-xl font-semibold">
                      {plans.find(p => p.id === billingInfo.subscription.plan)?.name || billingInfo.subscription.plan.toUpperCase()} 
                    </h3>
                    <Badge variant="outline" className={getStatusColor(billingInfo.subscription.status)}>
                      {billingInfo.subscription.status === 'active' ? '' :
                       billingInfo.subscription.status === 'trial' ? '' :
                       billingInfo.subscription.status === 'suspended' ? '' :
                       billingInfo.subscription.status === 'cancelled' ? '' : billingInfo.subscription.status}
                    </Badge>
                  </div>
                  <p className="text-gray-600">
                    {billingInfo.subscription.status === 'trial' && billingInfo.subscription.trialEnd ? (
                      <> : {new Date(billingInfo.subscription.trialEnd).toLocaleDateString('ko-KR')}</>
                    ) : (
                      <> : {new Date(billingInfo.subscription.currentPeriodEnd).toLocaleDateString('ko-KR')}</>
                    )}
                  </p>
                  {billingInfo.subscription.cancelAtPeriodEnd && (
                    <p className="text-orange-600 text-sm mt-1">
                        {new Date(billingInfo.subscription.currentPeriodEnd).toLocaleDateString('ko-KR')} .
                    </p>
                  )}
                </div>
                <div className="flex gap-2">
                  <Dialog open={isChangePlanDialogOpen} onOpenChange={setIsChangePlanDialogOpen}>
                    <DialogTrigger asChild>
                      <Button variant="outline"> </Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-4xl">
                      <DialogHeader>
                        <DialogTitle> </DialogTitle>
                        <DialogDescription>
                             
                        </DialogDescription>
                      </DialogHeader>
                      
                      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 py-6">
                        {plans.map((plan) => (
                          <div
                            key={plan.id}
                            className={`relative border rounded-lg p-6 cursor-pointer transition-all hover:shadow-md ${
                              selectedPlan === plan.id ? 'border-blue-500 ring-2 ring-blue-200' : 
                              plan.id === billingInfo.subscription.plan ? 'border-green-500' : 'border-gray-200'
                            } ${plan.popular ? 'ring-2 ring-blue-100' : ''}`}
                            onClick={() => setSelectedPlan(plan.id)}
                          >
                            {plan.popular && (
                              <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
                                <Badge className="bg-blue-600"></Badge>
                              </div>
                            )}
                            
                            {plan.id === billingInfo.subscription.plan && (
                              <div className="absolute top-3 right-3">
                                <Badge variant="outline" className="bg-green-100 text-green-700 border-green-300">
                                   
                                </Badge>
                              </div>
                            )}

                            <div className="text-center mb-6">
                              <h3 className="text-xl font-semibold mb-2">{plan.name}</h3>
                              <div className="mb-2">
                                <span className="text-3xl font-bold">{plan.price.toLocaleString()}</span>
                                <span className="text-gray-600">/{plan.period === 'month' ? '' : ''}</span>
                              </div>
                              <p className="text-sm text-gray-600">{plan.description}</p>
                            </div>

                            <ul className="space-y-2 mb-6">
                              {plan.features.map((feature, index) => (
                                <li key={index} className="flex items-start gap-2 text-sm">
                                  <Check className="h-4 w-4 text-green-500 mt-0.5 flex-shrink-0" />
                                  <span>{feature}</span>
                                </li>
                              ))}
                            </ul>
                          </div>
                        ))}
                      </div>

                      <DialogFooter>
                        <Button variant="outline" onClick={() => setIsChangePlanDialogOpen(false)}>
                          
                        </Button>
                        <Button
                          onClick={() => selectedPlan && handleChangePlan(selectedPlan)}
                          disabled={!selectedPlan || selectedPlan === billingInfo.subscription.plan || changePlan.isPending}
                        >
                          {changePlan.isPending ? ' ...' : ' '}
                        </Button>
                      </DialogFooter>
                    </DialogContent>
                  </Dialog>

                  {!billingInfo.subscription.cancelAtPeriodEnd && (
                    <Dialog open={isCancelDialogOpen} onOpenChange={setIsCancelDialogOpen}>
                      <DialogTrigger asChild>
                        <Button variant="outline" className="text-red-600 hover:text-red-700">
                           
                        </Button>
                      </DialogTrigger>
                      <DialogContent>
                        <DialogHeader>
                          <DialogTitle> </DialogTitle>
                          <DialogDescription>
                              ?         .
                          </DialogDescription>
                        </DialogHeader>
                        
                        <form onSubmit={cancelForm.handleSubmit(handleCancelSubscription)} className="space-y-4">
                          <div className="space-y-2">
                            <Label htmlFor="reason">  </Label>
                            <textarea
                              id="reason"
                              {...cancelForm.register('reason')}
                              placeholder="    ..."
                              className="w-full p-3 border rounded-md resize-none h-32"
                            />
                            {cancelForm.formState.errors.reason && (
                              <p className="text-sm text-red-600">
                                {cancelForm.formState.errors.reason.message}
                              </p>
                            )}
                          </div>

                          <DialogFooter>
                            <Button 
                              type="button" 
                              variant="outline" 
                              onClick={() => {
                                setIsCancelDialogOpen(false)
                                cancelForm.reset()
                              }}
                            >
                              
                            </Button>
                            <Button
                              type="submit"
                              variant="destructive"
                              disabled={cancelSubscription.isPending}
                            >
                              {cancelSubscription.isPending ? ' ...' : ' '}
                            </Button>
                          </DialogFooter>
                        </form>
                      </DialogContent>
                    </Dialog>
                  )}
                </div>
              </div>

              {/* Usage Stats */}
              <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
                <div className="text-center">
                  <div className="mb-2">
                    <div className="text-2xl font-bold text-gray-900">
                      {billingInfo.usage.users.current.toLocaleString()}
                    </div>
                    <div className="text-sm text-gray-600 flex items-center justify-center gap-1">
                      <Users className="h-3 w-3" />
                      / {billingInfo.usage.users.limit === -1 ? '' : billingInfo.usage.users.limit.toLocaleString()}
                    </div>
                  </div>
                  {billingInfo.usage.users.limit !== -1 && (
                    <Progress 
                      value={getUsagePercentage(billingInfo.usage.users.current, billingInfo.usage.users.limit)} 
                      className="h-2"
                    />
                  )}
                  <p className="text-xs text-gray-500 mt-1"></p>
                </div>

                <div className="text-center">
                  <div className="mb-2">
                    <div className="text-2xl font-bold text-gray-900">
                      {billingInfo.usage.campaigns.current.toLocaleString()}
                    </div>
                    <div className="text-sm text-gray-600 flex items-center justify-center gap-1">
                      <MessageSquare className="h-3 w-3" />
                      / {billingInfo.usage.campaigns.limit === -1 ? '' : billingInfo.usage.campaigns.limit.toLocaleString()}
                    </div>
                  </div>
                  {billingInfo.usage.campaigns.limit !== -1 && (
                    <Progress 
                      value={getUsagePercentage(billingInfo.usage.campaigns.current, billingInfo.usage.campaigns.limit)} 
                      className="h-2"
                    />
                  )}
                  <p className="text-xs text-gray-500 mt-1"></p>
                </div>

                <div className="text-center">
                  <div className="mb-2">
                    <div className="text-2xl font-bold text-gray-900">
                      {billingInfo.usage.contacts.current.toLocaleString()}
                    </div>
                    <div className="text-sm text-gray-600 flex items-center justify-center gap-1">
                      <UserCheck className="h-3 w-3" />
                      / {billingInfo.usage.contacts.limit === -1 ? '' : billingInfo.usage.contacts.limit.toLocaleString()}
                    </div>
                  </div>
                  {billingInfo.usage.contacts.limit !== -1 && (
                    <Progress 
                      value={getUsagePercentage(billingInfo.usage.contacts.current, billingInfo.usage.contacts.limit)} 
                      className="h-2"
                    />
                  )}
                  <p className="text-xs text-gray-500 mt-1"></p>
                </div>

                <div className="text-center">
                  <div className="mb-2">
                    <div className="text-2xl font-bold text-gray-900">
                      {billingInfo.usage.messages.current.toLocaleString()}
                    </div>
                    <div className="text-sm text-gray-600 flex items-center justify-center gap-1">
                      <TrendingUp className="h-3 w-3" />
                      / {billingInfo.usage.messages.limit === -1 ? '' : billingInfo.usage.messages.limit.toLocaleString()}
                    </div>
                  </div>
                  {billingInfo.usage.messages.limit !== -1 && (
                    <Progress 
                      value={getUsagePercentage(billingInfo.usage.messages.current, billingInfo.usage.messages.limit)} 
                      className="h-2"
                    />
                  )}
                  <p className="text-xs text-gray-500 mt-1"> ( )</p>
                </div>
              </div>

              {/* Upcoming Invoice */}
              {billingInfo.upcomingInvoice && (
                <div className="mt-6 p-4 bg-blue-50 rounded-lg">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="font-medium text-blue-900">  </h4>
                      <p className="text-sm text-blue-700">
                        {new Date(billingInfo.upcomingInvoice.date).toLocaleDateString('ko-KR')} - 
                        {billingInfo.upcomingInvoice.amount.toLocaleString()}
                      </p>
                    </div>
                    <Calendar className="h-5 w-5 text-blue-600" />
                  </div>
                </div>
              )}
            </>
          ) : null}
        </CardContent>
      </Card>

      {/* Payment Method */}
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <CreditCard className="h-5 w-5" />
            <div>
              <CardTitle> </CardTitle>
              <CardDescription>
                   
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {isLoading ? (
            <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
              <div className="flex items-center gap-3">
                <Skeleton className="w-8 h-6 rounded" />
                <div className="space-y-1">
                  <Skeleton className="h-4 w-32" />
                  <Skeleton className="h-3 w-20" />
                </div>
              </div>
              <Skeleton className="h-8 w-20" />
            </div>
          ) : billingInfo?.paymentMethod ? (
            <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
              <div className="flex items-center gap-3">
                <div className="w-8 h-6 bg-gray-200 rounded flex items-center justify-center text-xs font-medium">
                  {billingInfo.paymentMethod.brand.toUpperCase()}
                </div>
                <div>
                  <p className="font-medium">**** **** **** {billingInfo.paymentMethod.last4}</p>
                  <p className="text-sm text-gray-600">
                    : {billingInfo.paymentMethod.expiryMonth.toString().padStart(2, '0')}/{billingInfo.paymentMethod.expiryYear}
                  </p>
                </div>
              </div>
              <Dialog open={isPaymentDialogOpen} onOpenChange={setIsPaymentDialogOpen}>
                <DialogTrigger asChild>
                  <Button variant="outline" size="sm"></Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>  </DialogTitle>
                    <DialogDescription>
                         
                    </DialogDescription>
                  </DialogHeader>
                  
                  <form onSubmit={paymentForm.handleSubmit(handleUpdatePaymentMethod)} className="space-y-4">
                    <div className="space-y-2">
                      <Label> </Label>
                      <div className="p-4 border rounded-lg bg-gray-50">
                        <p className="text-sm text-gray-600 mb-2">
                            Stripe,     .
                        </p>
                        <Button 
                          type="button"
                          variant="outline" 
                          className="w-full"
                          onClick={() => paymentForm.setValue('token', 'mock_payment_token_' + Date.now())}
                        >
                          <CreditCard className="h-4 w-4 mr-2" />
                          / 
                        </Button>
                      </div>
                    </div>

                    <DialogFooter>
                      <Button 
                        type="button" 
                        variant="outline" 
                        onClick={() => {
                          setIsPaymentDialogOpen(false)
                          paymentForm.reset()
                        }}
                      >
                        
                      </Button>
                      <Button
                        type="submit"
                        disabled={updatePaymentMethod.isPending || !paymentForm.watch('token')}
                      >
                        {updatePaymentMethod.isPending ? ' ...' : ''}
                      </Button>
                    </DialogFooter>
                  </form>
                </DialogContent>
              </Dialog>
            </div>
          ) : (
            <div className="text-center p-8 bg-gray-50 rounded-lg">
              <CreditCard className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <h3 className="font-medium text-gray-900 mb-2">   </h3>
              <p className="text-gray-600 mb-4">
                     
              </p>
              <Dialog open={isPaymentDialogOpen} onOpenChange={setIsPaymentDialogOpen}>
                <DialogTrigger asChild>
                  <Button>  </Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>  </DialogTitle>
                    <DialogDescription>
                          
                    </DialogDescription>
                  </DialogHeader>
                  
                  <form onSubmit={paymentForm.handleSubmit(handleUpdatePaymentMethod)} className="space-y-4">
                    <div className="space-y-2">
                      <Label> </Label>
                      <div className="p-4 border rounded-lg bg-gray-50">
                        <p className="text-sm text-gray-600 mb-2">
                            Stripe,     .
                        </p>
                        <Button 
                          type="button"
                          variant="outline" 
                          className="w-full"
                          onClick={() => paymentForm.setValue('token', 'mock_payment_token_' + Date.now())}
                        >
                          <CreditCard className="h-4 w-4 mr-2" />
                          / 
                        </Button>
                      </div>
                    </div>

                    <DialogFooter>
                      <Button 
                        type="button" 
                        variant="outline" 
                        onClick={() => {
                          setIsPaymentDialogOpen(false)
                          paymentForm.reset()
                        }}
                      >
                        
                      </Button>
                      <Button
                        type="submit"
                        disabled={updatePaymentMethod.isPending || !paymentForm.watch('token')}
                      >
                        {updatePaymentMethod.isPending ? ' ...' : ''}
                      </Button>
                    </DialogFooter>
                  </form>
                </DialogContent>
              </Dialog>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Billing History */}
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <FileText className="h-5 w-5" />
            <div>
              <CardTitle> </CardTitle>
              <CardDescription>
                    
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {isLoading ? (
            <div className="space-y-3">
              {[...Array(3)].map((_, i) => (
                <div key={i} className="flex items-center justify-between p-4 border rounded-lg">
                  <div className="flex items-center gap-3">
                    <Skeleton className="w-6 h-6 rounded" />
                    <div className="space-y-1">
                      <Skeleton className="h-4 w-32" />
                      <Skeleton className="h-3 w-20" />
                    </div>
                  </div>
                  <div className="text-right space-y-1">
                    <Skeleton className="h-4 w-24" />
                    <Skeleton className="h-3 w-16" />
                  </div>
                </div>
              ))}
            </div>
          ) : billingInfo?.invoices && billingInfo.invoices.length > 0 ? (
            <div className="space-y-3">
              {billingInfo.invoices.map((invoice) => (
                <div key={invoice.id} className="flex items-center justify-between p-4 border rounded-lg hover:bg-gray-50">
                  <div className="flex items-center gap-3">
                    <FileText className="h-5 w-5 text-gray-400" />
                    <div>
                      <p className="font-medium">
                         #{invoice.id}
                      </p>
                      <p className="text-sm text-gray-600">
                        {new Date(invoice.date).toLocaleDateString('ko-KR')}
                      </p>
                    </div>
                  </div>
                  <div className="flex items-center gap-3">
                    <div className="text-right">
                      <p className="font-medium">{invoice.amount.toLocaleString()}</p>
                      <Badge 
                        variant="outline" 
                        className={getInvoiceStatusColor(invoice.status)}
                      >
                        {invoice.status === 'paid' ? '' :
                         invoice.status === 'pending' ? '' :
                         invoice.status === 'failed' ? '' : invoice.status}
                      </Badge>
                    </div>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => handleDownloadInvoice(invoice.id)}
                      disabled={downloadInvoice.isPending}
                    >
                      <Download className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center p-8">
              <FileText className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-600">  </p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}